.container {
  height: 100vh;
  width: 100%;
  background: radial-gradient(#e66465, #9198e5);
}
.bg-image {
  height: 100vh;
  width: 100%;
  opacity: 25%;
}
.main-logo {
  position: absolute;
  top: 50px;
  left: 50px;
  z-index: 2;
  background-color: #e66465;
}
.main_logo1 {
  justify-items: center;
}
.intro {
  margin: 1em;
  display: flex;
  flex-direction: column;
  font-size: 3em;
  letter-spacing: 10px;
  color: rgb(255, 255, 255);
  position: absolute;
  left: 10vh;
  top: 30vh;
  box-shadow: 5px 5px 10px black;
}
h1 {
  font-size: 75px;
  padding: 10px;
  margin: 10px;
  z-index: 2;
}
.nav-container {
  position: absolute;
  right: 450px;
  top: 0;  
  width: 500px;
  height: 100px;
  margin: 2em;
  font-size: 25px;
  z-index: 2;
}
.nav_ul {
  text-decoration: none;
  list-style: none;
  display: flex;
  gap: 2em;
  font-size: 2em;
  position: relative;
}
.li_About:hover {
  color:white;
}
.li_Skills:hover {
  color: white;
}
.li_Contact:hover {
  color: white;
}
.picture-container {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 50%;
  height: 50%;
}
.line1 {
  position: absolute;
  top: 100px;
  left: 100px;
  background-color: white;
}
.photo {
  position: absolute;
  left: 100px;
  top: 0;
  border-radius: 1em;
  height: 15em;
  width: 20em;
  object-fit: fill;
  box-shadow: 0px 3px 3px black;
  z-index: 1;
}
.photo2 {
  position: absolute;
  left: 1000px;
  top: 400px;
  border-radius: 1em;
  height: 15em;
  width: 25em;
  object-fit: fill;
  box-shadow: 0px 3px 3px black;
  z-index: 0;
}
.text_wrapper {
  z-index: 2;
}
.skills_content {
  display: flex;
  position: absolute;
  top: 25%;
  left: 400px;
  gap: 250px;
  z-index: 1;
}
.CT {
  margin-right: 2em;
}
.CA {
  margin-left: 2em;
}
.skills_ul {
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 25px;
  list-style: none;
}
.list_item {
  display: flex;
  padding: 10px;
}
svg {
  align-self: center;
}
.skills_title {
  font-size: 3em;
  color: white;
}
.aboutme_content {
  display: flex;
  position: absolute;
  top: 15%;
  left: 400px;
  gap: 250px;
}
.slide-1 {
  display: flex;
  position: relative;
  width: 700px;
  height: 700px;
}
.slide-2 {
  display: flex;
  position: relative;
  width: 700px;
  height: 700px;
}
p {
  color: white;
  position: absolute;
  top: 90%;
  left: -75px;
  right: 50px;
  font-size: 35px;
  background-color: rgb(219, 119, 139);
  padding: 10px;
  border-radius: 10px;
}
.img-scar {
  border-radius: 10px;
  width: 750px;
  height: 900px;
}
.img-grad {
  border-radius: 10px;
  width: 750px;
  height: 900px;
}
.contact_content {
  position: absolute;
  top: 25%;
  left: 25%;
  letter-spacing: 5px;
}